@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
:root {
  --font-primary: "Roboto", sans-serif;
  --color-text: #fff;
  --color-background: #15171f;
  --color-primary: #1e293b;
  --color-secondary: #202330;
  --color-button: #dfe0e2;
  --color-gray: #d1d5db;
  --color-gold: #b7a73f;
  --color-border: #383f47;
}

body {
  color: var(--color-text);
  font-size: 16px;
  font-family: var(--font-primary);
  background-color: var(--color-background);
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-weight: 500;
  line-height: 1.5;
  display: flex;
}

#root {
  text-align: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 16px;
}

p {
  color: var(--color-gray);
}

input {
  background-color: var(--color-secondary);
  box-sizing: border-box;
  color: var(--color-text);
  border: 1px solid #0000;
  border: 1px solid var(--color-border);
  border-radius: 8px;
  outline: none;
  width: 100%;
  margin: 16px 0;
  padding: 14px 20px;
  font-family: inherit;
  font-size: 15px;
  font-weight: 500;
  transition: border-color .25s;
}

input[type="text"]:focus {
  border: 1px solid var(--color-border);
  outline-offset: 2px;
  outline-width: 2px;
  outline-style: solid;
  outline-color: var(--color-border);
}

button {
  background-color: var(--color-button);
  color: var(--color-primary);
  cursor: pointer;
  border: none;
  border-radius: 8px;
  width: 96px;
  margin: 8px;
  padding: 6px 20px;
  font-size: 12px;
  font-weight: 400;
}

button:hover {
  background-color: #dfe0e2cc;
}

header {
  flex-flow: column wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  align-items: center;
  gap: 8px;
  display: flex;
}

.icon {
  width: 24px;
  height: 24px;
}

.icon .material-symbols-outlined {
  color: var(--color-gold);
}

h2 {
  font-size: 20px;
  font-weight: 700;
}

.unit {
  justify-content: center;
  align-items: center;
  padding: 24px 0;
  display: flex;
}

.unit > div {
  align-items: center;
  display: flex;
}

.unit-btn {
  border-radius: unset;
  background-color: var(--color-primary);
  color: var(--color-button);
  margin: unset;
  width: 84px;
  padding: 6px 20px;
}

.unit-btn:first-of-type {
  border-radius: 8px 0 0 8px;
}

.unit-btn:last-of-type {
  border-radius: 0 8px 8px 0;
}

.unit-btn:hover {
  background-color: #1e293bcc;
}

.active {
  background-color: var(--color-button);
  color: var(--color-primary);
}

.active:hover {
  background-color: var(--color-button);
  cursor: default;
}

main {
  flex-direction: column;
  align-items: center;
  padding-bottom: 32px;
  display: flex;
}

h3 {
  font-size: 22px;
  font-weight: 700;
}

.card {
  width: 100%;
  padding-bottom: 32px;
}

.add {
  background-color: var(--color-secondary);
  border-radius: 8px;
  width: 100%;
  min-width: 96px;
  box-shadow: 0 14px 40px #000;
}

.add > div > p {
  color: var(--color-text);
  padding: 6px;
}

.add > div:last-of-type {
  background-color: var(--color-primary);
  border-radius: 0 0 8px 8px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 16px;
  display: flex;
}

span {
  color: var(--color-text);
  font-weight: 700;
}

footer {
  justify-content: center;
  align-items: center;
  gap: 16px;
  padding: 24px;
  display: flex;
}

img {
  filter: invert(63%) sepia(41%) saturate(581%) hue-rotate(15deg) brightness(95%) contrast(91%);
}

@media (width >= 768px) {
  #root {
    padding: 32px;
    font-weight: 600;
  }

  input {
    margin: unset;
    width: 324px;
  }

  header {
    flex-direction: row;
  }

  main {
    flex-direction: row-reverse;
    justify-content: flex-end;
    align-items: flex-start;
    padding-top: 32px;
    display: flex;
  }

  .card {
    padding-bottom: unset;
  }

  h2 {
    font-size: 32px;
  }

  h3 {
    font-size: 24px;
  }

  body {
    font-size: 18px;
  }

  button {
    width: 180px;
    margin: 10px 8px;
    padding: 13px 20px;
    font-size: 15px;
    font-weight: 600;
  }

  .add {
    width: unset;
    min-width: 324px;
  }

  .add > div:last-of-type {
    flex-direction: column;
    padding: 32px;
  }

  .add > div > p {
    padding: 12px;
  }

  .unit {
    justify-content: flex-start;
  }
}

